import "./create-game.css"
import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav';
import manualImage from './image/manual-player.jpg'
import Footer from "../../../components/misc-items/footer/footer"
import CreateManualGamePopup from './create-manual-game-popup';
import { useState } from 'react';

export default function CreateGame() {
    const [isManualGameOpen, setIsManualGameOpen] = useState(false);

    return(
        <div className='create-game-page'>
            <div>
                <EndzoneNav links={[{title: "Return to Data Hub", link: "/endzone/data/hub"},]}></EndzoneNav>
            </div>
            <div className='create-game-body'>
                <div className='content-div' onClick={() => setIsManualGameOpen(true)}>
                    <div className='header'>
                        <h3>Manual Scouting</h3>
                        <p>Click to create a game with Manual Entry </p>
                    </div>
                    <div className='img-div' id = "manual-img"><img src={manualImage}></img></div>
                </div>
            </div>
            <div className='create-game-footer'>
                <a href='/endzone/data/manage'>Click to Load a Existing Game</a>
            </div>
            <CreateManualGamePopup open={isManualGameOpen} onClose={() => setIsManualGameOpen(false)}/>
            <Footer></Footer>
        </div>
    );
}