import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import Dialog from '@mui/material/Dialog';
import Button from '@mui/material/Button';
import CloseIcon from '@mui/icons-material/Close';
import AddIcon from '@mui/icons-material/Add';
import { DialogTitle } from '@mui/material';
import TextField from '@mui/material/TextField';
import { LocalizationProvider } from '@mui/x-date-pickers';
import { ThemeProvider, createTheme } from '@mui/material';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { DatePicker} from '@mui/x-date-pickers';
import { useState } from 'react';
import axios from 'axios';

export default function CreateManualGamePopup(props){
    const [awayTeam, setAwayTeam] = useState("");
    const [homeTeam, setHomeTeam] = useState("");
    const [gameDate, setGameDate] = useState("");
    const [errorMessage, setErrorMessage] = useState("");

    const createGame = (gameType) => {
        // Validation
        if (awayTeam.length == 0 || homeTeam.length == 0)
        {
            setErrorMessage("A Home Team and a Away Team are Needed")
            return
        }
        else if(awayTeam === homeTeam)
        {
            setErrorMessage("Away and Home teams cannot be the same, if a scrimmage please add some identifier for the two teams (Ex: Eastview Black, Eastview White).");
            return
        }
        else if(gameDate == "")
        {
            setErrorMessage("Please Select a Game Date")
            return
        }

        axios.post("/endzone/data/game/create", {
            home_team: homeTeam,
            away_team: awayTeam,
            game_date: gameDate.replaceAll("-", "/"),
            game_type: "manual"
        }).then((request) => {
            props.onClose()
            window.location.href = `/endzone/data/game/manual?homeTeam=${homeTeam}&awayTeam=${awayTeam}&gameId=${request}`
        }, (request) => setErrorMessage("Unexpected Error - Please Contact: endzone.support@gmail.com"))
    }




    const theme = createTheme({
        typography: {
          fontFamily: 'Audiowide',
          color: 'white'
        },
        overrides: {
            MuiPickersBasePicker: {
            pickerView: {
                padding: '1px', // Adjust the padding as needed
            },
            },
        }
      });

    return (
        <Dialog open={props.open} onClose={props.onClose}>
            <DialogTitle sx={{backgroundColor: "white"}}>
                <h3 style={{fontFamily: 'Audiowide', fontSize: '21px', color: 'black'}}>Create Game</h3>
            </DialogTitle>
            <DialogContent sx={{backgroundColor: "white", display: 'inline-block'}}>
                <div style={{width: '100%'}}>
                    <TextField
                        id="home-team"
                        label="Home Team"
                        size='small'
                        sx={{marginTop: '10px', color: 'black'}}
                        onChange = {(e) => setHomeTeam(e.target.value)} 
                        InputLabelProps={{
                            style: { fontFamily: 'Audiowide', color: 'black'}
                        }}
                        InputProps={{
                            style: { fontFamily: 'Audiowide', color: 'black'}
                        }}
                    />
                </div>
                <div style={{width: '100%'}}>
                    <TextField
                        id="away-team"
                        label="Away Team"
                        size='small'
                        onChange = {(e) => setAwayTeam(e.target.value)} 
                        sx={{marginTop: '10px'}}
                        InputLabelProps={{
                            style: { fontFamily: 'Audiowide', color: 'black'}
                        }}
                        InputProps={{
                            style: { fontFamily: 'Audiowide', color: 'black'}
                        }}
                    />
                </div>
                <div style={{width: '100%'}}>
                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                        <ThemeProvider theme={theme}>
                            <DatePicker 
                                label={<span style={{ color: 'white' }}>Game Date</span>}
                                format = "DD-MM-YYYY"
                                disableFuture = {true} 
                                onAccept={(date) =>  setGameDate(date.format("MM-DD-YYYY"))}
                                style={{ fontFamily: 'Audiowide'}}
                                sx={{marginTop: '10px'}}
                            >
                            </DatePicker>
                        </ThemeProvider>
                    </LocalizationProvider>
                </div>
                <p style={{color: '#0984e3'}}>{errorMessage}</p>
            </DialogContent>
            <DialogActions sx={{backgroundColor: "white"}}>
                <Button variant="contained" endIcon={<AddIcon />} sx={{backgroundColor: '#17181d', fontFamily: 'Audiowide', color: 'white'}} onClick={() => {createGame()}}>
                    Create Game
                </Button>
                <Button variant="contained" endIcon={<CloseIcon />} sx={{backgroundColor: '#17181d', fontFamily: 'Audiowide', color: 'white'}} onClick={props.onClose}>
                    Close
                </Button>
            </DialogActions>
        </Dialog>
    )
}